html {
  font-size: 16px; }

body {
  font-family: 'Roboto', 'Arial', sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word; }

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto;
  background-color: #333333; }

.wrap {
  min-width: 320px;
  max-width: 1200px;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto; }

.header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background-image: url("../img/slider/1.jpg");
  background-color: #333333;
  background-size: cover;
  height: 100vh;
  width: 100%;
  z-index: -1;
  transition: .3s all;
  animation-name: move;
  animation-duration: 20s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  filter: grayscale(30%) contrast(110%) brightness(95%); }

.header-active {
  background-size: 120%;
  background-position: 0% 100%;
  animation-name: move;
  animation-duration: 10s; }

@keyframes move {
  from {
    background-position: 0% 100%; }
  50% {
    background-position: 100% 100%; }
  to {
    background-position: 0% 100%; } }

.header-navigation {
  display: flex;
  justify-content: space-between;
  padding: 0 calc(50% - 160px);
  padding-top: 30px;
  position: relative;
  justify-content: center;
  align-items: center; }

.header-menu {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 60%;
  right: 0;
  left: 0;
  transition: 0.3s;
  transform: scaleY(0) translateY(0);
  transform-origin: top center;
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 20vh;
  line-height: 50px;
  background-color: none;
  margin-top: 45px;
  margin-right: -25px;
  border-radius: 5%; }

.menu-nav_active {
  transform: scaleY(1) translateX(0%);
  opacity: 1;
  visibility: visible; }

.header-menu-item {
  position: relative;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.2);
  transition: .5s all;
  text-decoration: none;
  color: #333333; }
  .header-menu-item::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0%;
    height: 1px;
    background-color: #fff;
    transition: .3s all;
    transform-origin: top center; }
  .header-menu-item:hover {
    background-color: rgba(255, 255, 255, 0);
    transition: .5s all; }
  .header-menu-item:hover::after {
    width: 100%;
    transition: .5s all; }

/* БУРГЕР */
.menu-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  background-color: #fff;
  border-radius: 50%;
  position: relative;
  z-index: 12;
  text-decoration: none;
  color: #111; }
  .menu-btn:hover::after {
    transform: rotateZ(360deg);
    transition: 3s all; }
  .menu-btn::after {
    content: "";
    position: absolute;
    top: -5px;
    left: -5px;
    width: 60px;
    height: 60px;
    background-image: url("/img/logotype/logo_part (2).png");
    background-size: cover;
    z-index: -2;
    transform: rotateZ(-360deg);
    transition: 3s all; }
  .menu-btn::before {
    content: "";
    position: absolute;
    top: -12px;
    left: -21px;
    width: 91px;
    height: 67px;
    background-image: url("/img/logotype/logo_part (1).png");
    background-size: cover;
    z-index: -2; }
  .menu-btn span {
    font-size: 20px; }

.header-title-block {
  position: relative;
  text-align: center;
  padding: 0 calc(50% - 160px); }

.header-main-title {
  font-family: 'Squada One', cursive;
  font-size: 90px;
  line-height: 100px;
  margin: 0;
  color: #fff;
  text-shadow: 5px 4px 4px rgba(51, 51, 51, 0.8); }

.header-main-subtitle {
  margin: 0;
  font-family: 'Great Vibes', cursive;
  color: #fff;
  font-size: 40px; }

.slider-menu {
  display: flex;
  justify-content: space-around;
  list-style: none;
  padding: 0;
  width: 120px; }
  .slider-menu .slider-menu-item {
    width: 15px;
    height: 15px;
    border: 1px solid #fff;
    border-radius: 50%;
    margin-bottom: 10px;
    cursor: pointer; }
  .slider-menu .item-active {
    background-color: #fff; }

body {
  background-color: #333333;
  position: relative; }

.wrap-block {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(33, 33, 33, 0.4);
  z-index: 20; }
  .wrap-block::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, 50%);
    width: 120px;
    height: 120px;
    background-image: url("/img/logotype/logo_part (2).png");
    background-size: cover;
    z-index: -3;
    transform: rotateZ(-360deg);
    transition: 3s all; }

main {
  margin-top: 100vh;
  z-index: 10;
  position: relative;
  background-color: #333333; }

section {
  padding: 10vh 0;
  position: relative;
  border-bottom: 10vh solid #222; }
  section:nth-child(odd) {
    border: none; }

.title {
  position: relative;
  margin: 0 0 35px 35px;
  color: #e5493a;
  font-size: 24px;
  text-transform: uppercase; }
  .title::after {
    content: "";
    position: absolute;
    bottom: -20px;
    left: 0;
    width: 120px;
    height: 3px;
    background-color: #e5493a; }

.about-section {
  display: flex;
  flex-direction: column;
  background-image: url("/img/fon/section-fon.jpg");
  background-size: cover;
  background-position-x: 60%; }

.about-title {
  position: relative;
  margin: 70px 0 35px 20px;
  color: #e5493a;
  font-size: 24px;
  text-transform: uppercase; }
  .about-title::after {
    content: "";
    position: absolute;
    bottom: -20px;
    left: 0;
    width: 120px;
    height: 3px;
    background-color: #e5493a; }

.about-text {
  font-size: 16px;
  padding: 15px 15px;
  text-align: justify;
  font-family: 'Roboto', sans-serif;
  color: #fff;
  font-size: 18px;
  font-weight: 300;
  margin: 0 15px;
  max-width: 430px; }

.companies-list {
  display: flex;
  flex-direction: row;
  list-style: none;
  padding: 15px 15px;
  max-width: 430px;
  margin: 0 15px; }

.companies-item {
  width: 90%;
  display: flex;
  justify-content: center; }
  .companies-item img {
    width: 60%; }

.products-block {
  display: grid;
  grid-template-columns: 300px;
  padding: 15px 0;
  justify-content: center; }

.product-item {
  max-width: 100%;
  padding: 10px;
  margin-bottom: 35px;
  border: 1px solid grey;
  border-radius: 2px;
  transition: .2s all; }
  .product-item:hover {
    box-shadow: 0px 0px 8px 5px rgba(0, 0, 0, 0.65);
    transition: .2s all; }

.product-img {
  width: 100%; }

.contact-section {
  width: 100%;
  height: 100vh;
  background-image: url("/img/decorative/curve-wood-texture-tekstura_black.jpg");
  background-size: cover;
  background-position-x: 50%; }

.footer {
  display: flex;
  background-image: url("/img/decorative/curve-wood-texture-tekstura_border.jpg");
  background-size: cover;
  background-position-x: 50%;
  height: 90px;
  justify-content: center;
  align-items: center;
  transition: .5s all; }
  .footer:hover {
    transform: skew(20px 30px);
    transition: .5s all; }

.footer-text {
  color: #fff;
  text-shadow: 0px 0px 20px #00c6ff; }

.spinner-overlay {
  display: none;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 10; }

.visible {
  display: block; }

.spinner {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.spiner-image {
  position: absolute;
  width: 100px;
  -webkit-animation: goGoGO 1s ease-out; }

.spiner-image_around {
  position: absolute;
  width: 150px;
  -webkit-animation: goGoGOBack 1s ease-out; }

@-webkit-keyframes goGoGO {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

@-webkit-keyframes goGoGOBack {
  0% {
    transform: scale(0); }
  100% {
    transform: scale(1); } }
